import React, { Component } from 'react';
import Blogentries from './Blogentries';

class Blog extends Component {

    render() {


        {/* to add new post just make a new entry in array */ }
        const blogentries = [
            {
                title: "Rajesh",
                description: "Some quick example text to build on the card title and make up the bulk of the card's content.",
                link: "Go Somewhere"

            },
            {
                title: "Rajesh",
                description: "Some quick example text to build on the card title and make up the bulk of the card's content.",
                link: "Go Somewhere"

            },
            {
                title: "Rajesh",
                description: "Some quick example text to build on the card title and make up the bulk of the card's content.",
                link: "Go Somewhere"

            }
        ]
        const blogentriesarray = blogentries.map((entry, i) => <Blogentries title={blogentries[i].title} description={blogentries[i].description} link={blogentries[i].link} />)

        return (
            <div className="container-fluid">
                <div className="row">
                    {/*wrap the Blogentries with Link tag to go somewhere */}
                    {blogentriesarray}
                </div>


            </div>

        )
    }


}

export default Blog;
