import React, { Component } from 'react'
import './index.css'
import 'bootstrap/dist/css/bootstrap.min.css';
import $ from 'jquery';
//import Popper from 'popper.js';
import 'bootstrap/dist/js/bootstrap.bundle.min';

import { Link } from 'react-router-dom'



class Menu extends Component {

    componentDidMount() {
        $(document).ready(function () {
            $(".text").hover(function () {
                $(this).toggleClass("hidden");
                $(this).css("font-size", "1em/1");
            });
        });
    }

    render() {
        return (
            <div className="Header" id="headernav">
                <nav className="navbar navbar-expand-lg container-fluid">
                    {/*navigation text effect left */}
                    <Link to="#" className="navbar-brand">
                        <ul className="text hidden">
                            <li>R</li>
                            <li className="ghost">A</li>
                            <li className="ghost">J</li>
                            <li className="ghost">E</li>
                            <li className="ghost">S</li>
                            <li className="ghost">H</li>
                            <li className="ghost">R</li>
                            <li>O</li>
                            <li>Y</li>
                            <li className="ghost">A</li>
                            <li className="ghost">L</li>

                        </ul>
                    </Link>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="fa fa-bars"></span>
                    </button>

                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav ml-auto">
                            <li className="nav-item"><Link to="/" className="first nav-link">Home</Link></li>
                            <li className="nav-item"><Link to="/about" className="second nav-link">About</Link></li>
                            <li className="nav-item"><Link to="/blog" className="third nav-link">Blog</Link></li>
                            <li className="nav-item"><Link to="/projects" className="forth nav-link">Project</Link></li>
                            <li className="nav-item"><Link to="/contact" className="fifth nav-link">Contact</Link></li>
                        </ul>
                    </div>
                </nav>
{/*
                <nav className="navbar navbar-default">
                    <div className="container-fluid">
                        <div className="navbar-header">

                        </div>
                        <ul className="nav navbar-nav" id="topnavbar">

                            <div className="underbar"></div>
                        </ul>
                    </div>
</nav> */}
            </div>
        )
    }
}

export default Menu;