import React, { Component } from 'react'
import './index.css'

class Contact extends Component {
    render() {
        return (
            <div className="container-fluid">

                <section class="mb-4">
                    {/*Section: Contact v.2*/}
                    {/*Section heading*/}
                    <h2 class="h1-responsive font-weight-bold text-center my-4">Contact Me</h2>
                    {/*Section description*/}
                    <p class="text-center w-responsive mx-auto mb-5">Do you have any questions? Please do not hesitate to contact Me directly. Our team will come back to you within
        a matter of hours to help you.</p>

                    <div class="row">

                        {/*Grid column*/}
                        <div class="col-md-9 mb-md-0 mb-5">
                            <form id="contact-form" name="contact-form">

                                {/*Grid row*/}
                                <div class="row">

                                    {/*Grid column*/}
                                    <div class="col-md-6">
                                        <div class="md-form mb-5">
                                            <input placeholder="Your Name" type="text" id="name" name="name" class="form-control" />
                                            
                                        </div>
                                    </div>
                                    {/*Grid column*/}

                                    {/*Grid column*/}
                                    <div class="col-md-6">
                                        <div class="md-form mb-5">
                                            <input placeholder="Your Email"type="text" id="email" name="email" class="form-control" />
                                           
                                        </div>
                                    </div>
                                    {/*Grid column*/}

                                </div>
                                {/*Grid row*/}

                                {/*Grid row*/}
                                <div class="row">
                                    <div class="col-md-12">
                                        <div class="md-form mb-5">
                                            <input placeholder="Your Subject" type="text" id="subject" name="subject" class="form-control" />
                                            
                                        </div>
                                    </div>
                                </div>
                                {/*Grid row*/}

                                {/*Grid row*/}
                                <div class="row">

                                    {/*Grid column*/}
                                    <div class="col-md-12">

                                        <div class="md-form">
                                            <textarea placeholder="Your Message" type="text" id="message" name="message" rows="8" class="form-control md-textarea"></textarea>
                                        </div>

                                    </div>
                                </div>
                                {/*Grid row*/}

                            </form>

                            <div class="text-center text-md-left">
                                <a class="btn btn-primary">Send</a>
                            </div>
                            <div class="status"></div>
                        </div>
                        {/*Grid column*/}

                        {/*Grid column*/}
                        <div class="col-md-3 text-center">
                            <ul class="list-unstyled mb-0">
                                <li className="borderleft"><i class="fa fa-map-marker fa-2x"></i>
                                    <p>Ajmer, Raj 305002, USA</p>
                                </li>

                                <li className="bordertop borderleft"><i class="fa fa-phone mt-4 fa-2x mt-5 pt-4"></i>
                                    <p>+91 8209 2069 12</p>
                                </li>

                                <li className="bordertop borderleft"><i class="fa fa-envelope mt-5 fa-2x pt-5"></i>
                                    <p>rajeshroyal896@gmail.com</p>
                                </li>
                            </ul>
                        </div>
                        {/*Grid column*/}

                    </div>

                </section>
                {/*Section: Contact v.2*/}
            </div>
        )
    }
}

export default Contact;